import React, { useState } from 'react';
import styles from './PropBetListItem.module.css';

interface Props {
    id : number;
    expanded : boolean;
}

const PropListBody: React.FC<Props> = (props) => {
    let className;
    if (props.expanded) {
        className = styles.bodyCollapsed;
    } else {
        className = styles.bodyExpanded;
    }
    return <div className={className}></div>
};

export default PropListBody;